<template>
  <!-- hash -->
  <h2 v-if="item.type === 'hash'" class="item-hash" :id="item.text | generateId" :data-text="item.text">
    <router-link class="header-anchor" :to="{ path: $route.path, hash: `#${item.text}` }">
      {{ item.text }}
    </router-link>
  </h2>
  <div v-else class="content-item">
    <!-- title -->
    <p v-if="item.type === 'title'" class="item-title">{{ item.text }}</p>

    <!-- 普通文本 -->
    <div v-if="item.type === 'normal'" class="item-normal">
      <p :style="item.style || {}" v-if="item.text">{{ item.text }}</p>
      <ul v-if="Array.isArray(item.children)" style="margin-left: -10px;">
        <li v-for="(c1, i) in item.children" :key="i">
          <span v-if="c1.type === 'html'" v-html="c1.text" />
          <span v-else>{{ c1.text }}</span>
          <ul v-if="Array.isArray(c1.children)" style="margin-left: -15px; list-style-type: disc">
            <li v-for="(c2, j) in c1.children" :key="j">{{ c2.text }}</li>
          </ul>
        </li>
      </ul>
    </div>

    <!-- html 类型 -->
    <p v-if="item.type === 'html'" v-html="item.text"></p>

    <!-- warning 类型 -->
    <p v-if="item.type === 'warning'" class="item-warning-content">{{ item.text }}</p>

    <!-- code 类型 -->
    <p v-if="item.type === 'code'" class="item-code-content" style="position: relative">
      <i 
        class="el-icon-copy-document"
        v-clipboard:copy="item.text"
        v-clipboard:success="onCopy"
        v-clipboard:error="onError"
      />
      {{ item.text }}
    </p>

    <!-- demo 类型：用于展示具体的接口信息和代码 -->
    <Demo v-if="item.type === 'demo'" :lang="lang" :item="item" />
  </div>
</template>

<script>
import { msgMap, generateId } from '@/utils'

import Demo from './Demo.vue'

export default {
  name: "ContentItem",
  props: {
    lang: String,
    item: {
      type: Object,
      default: () => {}
    }
  },

  components: {
    Demo,
  },

  data() {
    return {
     
    }
  },

  filters: {
    generateId
  }, 

  computed: {
    successMsg() {
      return msgMap['success'][this.lang]
    },

    errorMsg() {
      return msgMap['error'][this.lang]
    },
  },

  methods: {
    onCopy() {
      this.$message.success(this.successMsg)
    },
    onError() {
      this.$message.success(this.errorMsg)
    },
  },
}
</script>

<style>
.content-item {}

.item-hash {
  margin-top: 40px;
  margin-bottom: 20px;
  font-weight: 700;
  font-size: 24px;
  line-height: 36px;
  color: #000000;
}

.item-hash a, .item-hash a:hover {
  text-decoration: none;
  color: #000000;
}

.item-title {
  font-weight: 700;
  font-size: 24px;
  line-height: 36px;
  color: black;
  margin: 20px 0px;
}

.item-warning-content {
  display: inline-block;
  /* margin: 16px 0px; */
  margin: 16px 0 0 0;
  background: #FFD677;
  border-radius: 8px;
  padding: 16px;
}

.span-code {
  padding: 2px 4px;
  color: #1f1e24;
  background-color: #ececef;
  border-radius: 4px;
}

.item-code-content {
  background: #F9F9F9;
  border-radius: 8px;
  padding: 24px;
}
.item-code-content i {
  position: absolute;
  top: 5px;
  right: 10px;
  cursor: pointer;
}
</style>


