<template>
  <div class="item-demo-content">
    <div>
      <div style="display: flex;">
        <div style="flex: 1">
          <el-radio v-model="parmasShowType" :label="1">Table</el-radio>
          <el-radio v-model="parmasShowType" :label="2">JSON</el-radio>
        </div>
        <el-button v-if="!isTable" size="small" v-clipboard:copy="jsonParams" v-clipboard:success="onCopy"
          v-clipboard:error="onError">
          {{ copyParmsBtnText }}
        </el-button>
      </div>
      <div class="params-content">
        <el-tabs v-model="activeParamsTab" type="card">
          <el-tab-pane v-for="tab in paramsTabs" :key="tab.key" :name="tab.key" :label="tab.type">
            <el-table v-if="isTable" :data="renderParamsData" stripe border>
              <el-table-column v-for="(column, index) in paramsTableColumn" :key="index" :label="column.label"
                :prop="column.prop" align="left" />
            </el-table>
            <div v-else class="code-html">
              <pre><code v-html="renderParamsData" class="hljs language-json" /></pre>
            </div>
          </el-tab-pane>
        </el-tabs>
      </div>
    </div>
    <div>
      <div style="display: flex; flex-direction: row-reverse;">
        <el-button size="small" v-clipboard:copy="code" v-clipboard:success="onCopy" v-clipboard:error="onError">{{
          copyCodeBtnText }}</el-button>
      </div>
      <div class="params-content" style="margin-top: 4px;">
        <el-tabs v-model="activeCodeTab" type="card">
          <el-tab-pane v-for="code in codeLangConfig" :key="code.value" :name="code.value" :label="code.label">
            <div class="code-html demo-code">
              <pre><code v-html="demoCode" :class="['hljs', `language-${activeCodeTab}`]" /></pre>
            </div>
          </el-tab-pane>
        </el-tabs>
      </div>
    </div>
  </div>
</template>

<script>
import { LANG_KEY, codeLangConfig, generateCode, msgMap } from '@/utils'

const propMap = {
  [LANG_KEY.zh]: {
    name: '参数名',
    type: '类型',
    required: '是否必填',
    description: '字段说明',
  },
  [LANG_KEY.tw]: {
    name: '參數名',
    type: '類型',
    required: '是否必填',
    description: '欄位說明',
  },
  [LANG_KEY.en]: {
    name: 'Parameter Name',
    type: 'Type',
    required: 'Required',
    description: 'Field Description',
  },
}
const wssMap = {
  [LANG_KEY.zh]: {
    name: '参数名',
    description: '字段说明',
  },
  [LANG_KEY.tw]: {
    name: '參數名',
    description: '欄位說明',
  },
  [LANG_KEY.en]: {
    name: 'Parameter Name',
    description: 'Field Description',
  },
}
const commonMap = {
  [LANG_KEY.zh]: {
    name: '',
    description: '说明',
  },
  [LANG_KEY.tw]: {
    name: '',
    description: '說明',
  },
  [LANG_KEY.en]: {
    name: '',
    description: 'description',
  },
}

const buttonTextMap = {
  'code': {
    [LANG_KEY.zh]: '复制代码',
    [LANG_KEY.tw]: '複製代碼',
    [LANG_KEY.en]: 'Copy Code',
  },
  'params': {
    [LANG_KEY.zh]: '复制参数',
    [LANG_KEY.tw]: '複製參數',
    [LANG_KEY.en]: 'Copy Parameters',
  }
}

const paramsTabKey = {
  request: 'request',
  request1: 'request1',
  request2: 'request2',
  request3: 'request3',
  response: 'response',
  response1: 'response1',
  response2: 'response2',
  response3: 'response3',
  wssResponse: 'wssResponse',
  common: 'common',
  key1: 'key1',
  key2: 'key2',
  key3: 'key3',
  key4: 'key4',
  key5: 'key5',
  key6: 'key6',
  key7: 'key7',
  key8: 'key8'
}

const host = "open-api.bingx.com"

export default {
  name: "Demo",
  props: {
    lang: String,
    item: {
      type: Object,
      default: () => { }
    }
  },

  data() {
    return {
      parmasShowType: 1,
      activeParamsTab: '',
      paramsTableColumn: [],

      codeLangConfig,
      activeCodeTab: codeLangConfig[0].value,
    }
  },

  computed: {
    isTable() {
      return this.parmasShowType === 1
    },

    successMsg() {
      return msgMap['success'][this.lang]
    },

    errorMsg() {
      return msgMap['error'][this.lang]
    },

    copyCodeBtnText() {
      return buttonTextMap['code'][this.lang]
    },

    copyParmsBtnText() {
      return buttonTextMap['params'][this.lang]
    },

    currentMap() {
      return propMap[this.lang]
    },
    currentCommonMap() {
      return commonMap[this.lang]
    },
    currentWssMap() {
      return wssMap[this.lang]
    },
    requestTableColumns() {
      return [{
        prop: 'name',
        label: this.currentMap['name'],
      }, {
        prop: 'type',
        label: this.currentMap['type'],
      }, {
        prop: 'required',
        label: this.currentMap['required'],
      }, {
        prop: 'description',
        label: this.currentMap['description'],
      }]
    },
    commonTableColumns() {
      return [{
        prop: 'name',
        label: this.currentCommonMap['name'],
      }, {
        prop: 'description',
        label: this.currentCommonMap['description'],
      }]
    },
    wssTableColumns() {
      return [{
        prop: 'name',
        label: this.currentWssMap['name'],
      }, {
        prop: 'description',
        label: this.currentWssMap['description'],
      }]
    },

    responseTableColumns() {
      return [{
        prop: 'name',
        label: this.currentMap['name'],
      }, {
        prop: 'type',
        label: this.currentMap['type'],
      }, {
        prop: 'description',
        label: this.currentMap['description'],
      }]
    },

    paramsTabs() {
      if (Array.isArray(this.item.params)) {
        return this.item.params.map(item => ({
          type: item.type,
          key: item.key
        }))
      }
      return []
    },

    jsonParams() {
      const item = this.item.params.find(item => item.key === this.activeParamsTab)
      const res = Object.create(null)
      const data = item ? item.data : []
      data.forEach(i => {
        res[i.name] = i.type
      })
      return JSON.stringify(res, null, 2)
    },

    renderParamsData() {
      const item = this.item.params.find(item => item.key === this.activeParamsTab)
      if (this.isTable) {
        return item ? item.data : []
      }

      const res = Object.create(null)
      const data = item ? item.data : []
      data.forEach(i => {
        res[i.name] = i.type
      })

      return this.$hljs.highlight(JSON.stringify(res, null, 2), { language: 'json' }).value
    },

    code() {
      if (!this.item.api) {
        return 'No Code'
      }
      return generateCode(this.item.api, this.activeCodeTab, this.item.host || host)
    },

    demoCode() {
      return this.$hljs.highlight(this.code, { language: this.activeCodeTab }).value
    }
  },

  watch: {
    activeParamsTab(val) {
      if (val === paramsTabKey.request || val === paramsTabKey.request1 || val === paramsTabKey.request2 || val === paramsTabKey.request3) {
        this.paramsTableColumn = [...this.requestTableColumns]
      } else if (val === paramsTabKey.response || val === paramsTabKey.response1 || val === paramsTabKey.response2 || val === paramsTabKey.response3) {
        this.paramsTableColumn = [...this.responseTableColumns]
      } else if (val === paramsTabKey.common) {
        this.paramsTableColumn = [...this.commonTableColumns]
      } else if (val === paramsTabKey.key1) {
        this.paramsTableColumn = [...this.commonTableColumns]
      } else if (val === paramsTabKey.key2) {
        this.paramsTableColumn = [...this.commonTableColumns]
      } else if (val === paramsTabKey.key3) {
        this.paramsTableColumn = [...this.commonTableColumns]
      } else if (val === paramsTabKey.key4) {
        this.paramsTableColumn = [...this.commonTableColumns]
      } else if (val === paramsTabKey.key5) {
        this.paramsTableColumn = [...this.commonTableColumns]
      } else if (val === paramsTabKey.key6) {
        this.paramsTableColumn = [...this.commonTableColumns]
      } else if (val === paramsTabKey.key7) {
        this.paramsTableColumn = [...this.commonTableColumns]
      } else if (val === paramsTabKey.key8) {
        this.paramsTableColumn = [...this.commonTableColumns]
      } else if (val === paramsTabKey.wssResponse) {
        this.paramsTableColumn = [...this.wssTableColumns]
      } else {
        this.paramsTableColumn = [...this.responseTableColumns]
      }
    },
  },

  methods: {
    onCopy() {
      this.$message.success(this.successMsg)
    },
    onError() {
      this.$message.success(this.errorMsg)
    },
  },

  created() {
    if (this.item.type === 'demo') {
      this.activeParamsTab = this.paramsTabs[0].key
    }
  }
}
</script>

<style scoped>
.item-demo-content {
  background: #F9F9F9;
  border-radius: 8px;
  display: flex;
  flex-direction: row;
  padding: 24px 0;
}

.item-demo-content>div {
  margin: 0 24px;
  width: 50%;
  height: 768px;
  overflow: hidden;
}

.params-content {
  margin-top: 15px;
}

.params-content>>>.el-tabs__header {
  margin: 0px;
}

.params-content>>>.el-tabs__content {
  background-color: #FFFFFF;
  padding: 15px;
  height: 690px;
  overflow: auto;
}

.params-content>>>.el-tabs__item.is-active {
  background-color: #FFFFFF;
}

.code-html {
  margin: 0 15px 0 15px;
  color: #FFFFFF;
  background-color: #282c34;
  height: 654px;
  overflow: hidden;
}

.code-html pre {
  margin: 0;
  padding: 0;
}

.code-html code {
  height: 654px;
  overflow: auto;
}
</style>


