<template>
  <div class="app-content">
    <h1 class="page-content-title">{{ pageContent.name }}</h1>
    <div class="page-content-wrap" id="page-content-wrap">
      <template v-if="Array.isArray(pageContent.content)">
        <ContentItem v-for="(item, index) in pageContent.content" :key="index" :item="item" :lang="lang" />
      </template>
      <p v-else>{{ pageContent.content }}</p>
    </div>
  </div>
</template>

<script>
import ContentItem from './ContentItem'

import { getAppConfig, emitter } from '@/utils'

const defaultPageContent = {
  name: 'Content Not Found',
  content: [],
}

export default {
  props: {
    lang: String
  },

  components: {
    ContentItem
  },

  data() {
    return {
      appConfig: getAppConfig(),
      pageContent: {
        ...defaultPageContent
      },
    }
  },

  computed: {
    // appConfig
  },

  watch: {
    '$route.path': {
      handler(val) {
        this.pageContent = this.appConfig[val] || { ...defaultPageContent }
      },
      immediate: true,
    },
    lang() {
      this.appConfig = getAppConfig()
      this.pageContent = this.appConfig[this.$route.path] || { ...defaultPageContent }
    }
  },

  mounted() {
    emitter.$emit('content-ready')
  },

  updated() {
    emitter.$emit('content-ready')
  }
}
</script>
<style scoped>
.app-content {
  padding: 0 28px;
}

.page-content-title {
  font-weight: 700;
  font-size: 32px;
  padding-bottom: 32px;
  line-height: 40px;
  color: #000;
  border-bottom: 1px solid #f0f0f0;
}

</style>


